body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/*
*
* ==========================================
* CUSTOM UTIL CLASSES
* ==========================================
*
*/

.vertical-nav {
    min-width: 18rem;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.1);
    transition: all 0.4s;
    background: #392F5A;
    font-family: 'Poppins';
  }
  
.page-content {
  width: calc(100% - 17rem);
  margin-left: 17rem;
  transition: all 0.4s;
}

.media-body {
  margin-right: 45%;
}

.media-body-heading {
    font-size: 30px;
}
.nav-item {
    font-size: 22px;
}
.nav-link {
    display: flex;
}
.nav-redirect {
  text-decoration: none;
  color: #fff;
}
.navbar-text {
    margin-left: 10px;
}
  /* for toggle behavior */
  
  #sidebar.active {
    margin-left: -17rem;
  }
  
  #content.active {
    width: 100%;
    margin: 0;
  }
  
  @media (max-width: 768px) {
    #sidebar {
      margin-left: -17rem;
    }
    #sidebar.active {
      margin-left: 0;
    }
    #content {
      width: 100%;
      margin: 0;
    }
    #content.active {
      margin-left: 17rem;
      width: calc(100% - 17rem);
    }
  }

.fileList {
    margin: 0;
  }
  
  .dropzoneDrag {
    font-size: 2rem;
    color: #777;
  }
  
  .FileDropzone {
    margin-top: 10%;
  }
  .dropzone {
    text-align: center;
    padding: 20px;
    background-color: #E6E1F8;
    color: #bdbdbd;
    margin-bottom: 20px;
    height: 500px;
    width: 70%;
    outline: 1px dashed #000000;
    outline-offset: -20px;
  }
  
  .dropzoneContent {
    margin-top: 35%;
  }

  .dropzoneText:hover {
    color: #fff;
  }

  .dropzoneText {
    font-family: 'Poppins';
    font-size: 24px;
    color: #878787;
    font-weight: 800;
    padding-top: 2%;
  }

  .accept {
    border-color: #107c10 !important;
  }
  
  .reject {
    border-color: #d83b01 !important;
  }
  
  .UploadPage {
    background-color: #FFE3F5;
    padding-top: 10px;
    margin-top: 8%;
    height: 100%;
  }
.fileCollection {
    background-color: #FFE3F5;
    margin-top: 8%;
    height: 750px;
    margin-left: auto!important;
}

.AudioFile {
    background: #392F5A;
    height: 200px;
    width: 200px;
    margin: 5%;
}

.FileHeader {
    color: #fff;
    font-family: 'Poppins';
    font-size: 21px;
    padding-top: 5%;
    font-weight: 800;
}

.AudioFile {
    margin-top: 30px;
}

.DeleteFile {
    height: 55px;
    width: 55px;
    background-color: #EEC8E0;
    border-radius: 50%;
    display: inline-block;
    margin-left: 72%;
}

.PromptHeader {
    color: #fff;
    font-family: 'Poppins';
    font-size: 14px;
    padding-top: 5%;
    font-weight: 500;
}
